import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import {
  Box,
  Container,
  Grid,
  Typography,
  Button,
  Card,
  CardMedia,
  Rating,
  Chip,
  TextField,
  Alert,
  CircularProgress,
  Divider,
  Paper,
  Avatar,
} from '@mui/material';
import { ShoppingCart, ArrowBack } from '@mui/icons-material';
import { fetchProduct, createReview } from '../store/slices/productSlice';
import { addToCart } from '../store/slices/cartSlice';
import { toast } from 'react-toastify';

const ProductDetailPage = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { product, loading } = useSelector((state) => state.products);
  const { user, isAuthenticated } = useSelector((state) => state.auth);

  const [quantity, setQuantity] = useState(1);
  const [selectedImage, setSelectedImage] = useState(0);
  const [reviewForm, setReviewForm] = useState({
    rating: 5,
    comment: '',
  });

  useEffect(() => {
    dispatch(fetchProduct(id));
  }, [dispatch, id]);

  const handleAddToCart = () => {
    dispatch(addToCart({
      product: product._id,
      name: product.name,
      image: product.images[0],
      price: product.price,
      quantity: quantity,
    }));
    toast.success('Product added to cart!');
  };

  const handleReviewSubmit = (e) => {
    e.preventDefault();
    if (!isAuthenticated) {
      toast.error('Please login to write a review');
      return;
    }
    dispatch(createReview({
      productId: product._id,
      rating: reviewForm.rating,
      comment: reviewForm.comment,
    }));
    setReviewForm({ rating: 5, comment: '' });
    toast.success('Review submitted successfully!');
  };

  if (loading || !product) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="50vh">
        <CircularProgress />
      </Box>
    );
  }

  // Price formatting
  const priceParts = product.price.toString().split('.');
  const wholePrice = priceParts[0];
  const fractionPrice = priceParts[1] || '00';

  return (
    <Container maxWidth="xl" sx={{ py: 4, bgcolor: 'white' }}>
      <Button
        startIcon={<ArrowBack />}
        onClick={() => navigate(-1)}
        sx={{ mb: 2, color: '#565959' }}
      >
        Back to results
      </Button>

      <Grid container spacing={4}>
        {/* Left: Image Gallery */}
        <Grid item xs={12} md={5}>
          <Box sx={{ display: 'flex', gap: 2 }}>
            {/* Thumbnails */}
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              {product.images.map((image, index) => (
                <Box
                  key={index}
                  onMouseEnter={() => setSelectedImage(index)}
                  sx={{
                    width: 50,
                    height: 60,
                    border: selectedImage === index ? '2px solid #e77600' : '1px solid #ccc',
                    borderRadius: 1,
                    cursor: 'pointer',
                    overflow: 'hidden',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center'
                  }}
                >
                  <img src={image} alt="" style={{ maxWidth: '100%', maxHeight: '100%' }} />
                </Box>
              ))}
            </Box>
            {/* Main Image */}
            <Box sx={{ flexGrow: 1, display: 'flex', justifyContent: 'center', alignItems: 'flex-start' }}>
              <img
                src={product.images[selectedImage]}
                alt={product.name}
                style={{ maxWidth: '100%', maxHeight: '500px', objectFit: 'contain' }}
              />
            </Box>
          </Box>
        </Grid>

        {/* Center: Details */}
        <Grid item xs={12} md={4}>
          <Typography variant="h5" component="h1" gutterBottom sx={{ fontWeight: 400, color: '#0F1111' }}>
            {product.name}
          </Typography>

          <Typography variant="body2" color="primary" sx={{ mb: 1, cursor: 'pointer', '&:hover': { textDecoration: 'underline', color: '#c7511f' } }}>
            Visit the {product.brand} Store
          </Typography>

          <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
            <Rating value={product.rating} readOnly precision={0.5} size="small" />
            <Typography variant="body2" sx={{ ml: 1, color: '#007185', cursor: 'pointer', '&:hover': { textDecoration: 'underline', color: '#c7511f' } }}>
              {product.numReviews} ratings
            </Typography>
          </Box>

          <Divider sx={{ my: 2 }} />

          <Box sx={{ display: 'flex', alignItems: 'baseline', mb: 1 }}>
            <Typography variant="h5" sx={{ color: '#CC0C39', fontWeight: 300 }}>-{product.discountPercentage}%</Typography>
            <Typography variant="h4" sx={{ ml: 1, display: 'flex', alignItems: 'flex-start' }}>
              <span style={{ fontSize: '14px', paddingTop: '5px' }}>$</span>
              {wholePrice}
              <span style={{ fontSize: '14px', paddingTop: '5px' }}>{fractionPrice}</span>
            </Typography>
          </Box>

          {product.originalPrice && (
            <Typography variant="body2" color="text.secondary" sx={{ display: 'flex', gap: 0.5 }}>
              List Price: <span style={{ textDecoration: 'line-through' }}>${product.originalPrice}</span>
            </Typography>
          )}

          <Typography variant="body2" sx={{ mt: 2, mb: 2 }}>
            {product.description}
          </Typography>

          {/* Specifications Table */}
          {product.specifications && (
            <Box sx={{ mt: 2 }}>
              <Typography variant="h6" sx={{ fontSize: '1rem', mb: 1 }}>Product specifications</Typography>
              <Grid container spacing={1}>
                {Object.entries(product.specifications).map(([key, value]) => (
                  <React.Fragment key={key}>
                    <Grid item xs={5}><Typography variant="body2" sx={{ fontWeight: 'bold', color: '#565959' }}>{key}</Typography></Grid>
                    <Grid item xs={7}><Typography variant="body2">{value}</Typography></Grid>
                  </React.Fragment>
                ))}
              </Grid>
            </Box>
          )}
        </Grid>

        {/* Right: Buy Box */}
        <Grid item xs={12} md={3}>
          <Paper elevation={0} variant="outlined" sx={{ p: 2, borderRadius: 2 }}>
            <Box sx={{ display: 'flex', alignItems: 'flex-start' }}>
              <span style={{ fontSize: '14px', paddingTop: '5px' }}>$</span>
              <Typography variant="h5" sx={{ fontWeight: 500 }}>{wholePrice}</Typography>
              <span style={{ fontSize: '14px', paddingTop: '5px' }}>{fractionPrice}</span>
            </Box>

            <Box sx={{ mt: 1 }}>
              <Typography variant="body2" sx={{ color: '#565959' }}>
                Delivery <span style={{ fontWeight: 'bold', color: '#0F1111' }}>Tuesday, Sep 19</span>
              </Typography>
              <Typography variant="caption" sx={{ color: '#007185', display: 'block', mt: 0.5 }}>
                Deliver to {isAuthenticated ? user?.name : 'Select Location'}
              </Typography>
            </Box>

            <Typography variant="h6" sx={{ color: product.stock > 0 ? '#007600' : '#B12704', fontSize: '18px', my: 2 }}>
              {product.stock > 0 ? 'In Stock' : 'Currently Unavailable'}
            </Typography>

            {product.stock > 0 && (
              <Box>
                <TextField
                  select
                  fullWidth
                  size="small"
                  value={quantity}
                  onChange={(e) => setQuantity(e.target.value)}
                  SelectProps={{ native: true }}
                  sx={{ mb: 2 }}
                >
                  {[...Array(Math.min(10, product.stock)).keys()].map((x) => (
                    <option key={x + 1} value={x + 1}>Qty: {x + 1}</option>
                  ))}
                </TextField>

                <Button
                  fullWidth
                  variant="contained"
                  color="secondary"
                  sx={{ mb: 1, borderRadius: 5, textTransform: 'none' }}
                  onClick={handleAddToCart}
                >
                  Add to Cart
                </Button>
                <Button
                  fullWidth
                  variant="contained"
                  sx={{ bgcolor: '#ffa41c', '&:hover': { bgcolor: '#fa8900' }, color: 'black', borderRadius: 5, textTransform: 'none' }}
                >
                  Buy Now
                </Button>
              </Box>
            )}

            <Box sx={{ mt: 2, fontSize: '12px', color: '#565959' }}>
              <Grid container>
                <Grid item xs={4}>Ships from</Grid>
                <Grid item xs={8}>Amazon.com</Grid>
                <Grid item xs={4}>Sold by</Grid>
                <Grid item xs={8}>Amazon.com</Grid>
              </Grid>
            </Box>
          </Paper>
        </Grid>

      </Grid>

      {/* Reviews Section (kept simple but styled) */}
      <Divider sx={{ my: 4 }} />
      <Box sx={{ maxWidth: '800px' }}>
        <Typography variant="h5" gutterBottom sx={{ fontWeight: 700 }}>Customer Reviews</Typography>
        {/* ... Existing Review Logic ... */}
        {/* Reusing existing review logic for brevity, just keeping it in existing container structure */}
        {product.reviews.length === 0 ? (
          <Alert severity="info" sx={{ mt: 2 }}>No reviews yet. Be the first to review!</Alert>
        ) : (
          <Box sx={{ mt: 2 }}>
            {product.reviews.map((review, index) => (
              <Box key={index} sx={{ mb: 3 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                  <Avatar sx={{ mr: 2, width: 30, height: 30 }}>
                    {review.name.charAt(0).toUpperCase()}
                  </Avatar>
                  <Typography variant="subtitle2">{review.name}</Typography>
                </Box>
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  <Rating value={review.rating} readOnly size="small" />
                  <Typography variant="caption" sx={{ ml: 1, fontWeight: 'bold' }}>Verified Purchase</Typography>
                </Box>

                <Typography variant="body2" color="text.secondary" sx={{ mt: 0.5, mb: 1 }}>
                  Reviewed on {new Date(review.createdAt).toLocaleDateString()}
                </Typography>
                <Typography variant="body1">
                  {review.comment}
                </Typography>
              </Box>
            ))}
          </Box>
        )}
      </Box>
    </Container>
  );
};

export default ProductDetailPage;
